for(var category in icons){
	icons[category].category = category;
}

export default icons;

export var extensionsMap = {};

for(var cat in extensions){
    for(var i in extensions[cat]){
        var ext = extensions[cat][i];
        extensionsMap[ext] = cat;
    }
}

export function getIconFromExt(ext){
    ext = ext.toLowerCase();
    var cat = extensionsMap[ext];
    var svgIcon = icons[cat] || icons.other;
    return svgIcon;
}